html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, --font-family-gabriola, --font-family-lato, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.body {
  margin: 0;
  //font-family: "Ubuntu", "Gabriola", sans-serif;
  font-weight: 400;
  overflow-x: hidden;
  background-color: var(--gray-color);

}
/* all :hover rules need to be prepended with body.hasHover */
body.hasHover a:hover {
  color: blue;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  position: relative;
  width: 100%;
  overflow: hidden;
  margin: 0 auto;
  padding: 0 15px;
   // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1560px;
}
.container-fluid {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 100%;
  background-color: inherit;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}
.flower {
  width: 60px;
  height: 60px;
  background: url("../img/flower.png") no-repeat;
  background-position: center;

}
.down-wrap {
  position: relative;
  width: auto;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}
.down {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 3000;
  width: 100px;
  height: 80px;
  text-align: center;
  //border-top-left-radius: 18px;
  background-image: url(../img/down.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
svg {
